                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <p class=".body-1">1. <code>indexdb</code>是浏览器端的数据库，就是超级缩减版的redis，包含的功能只局限在 游标，索引，表，事务</p>
                    <p class=".body-1">2. <code>indexdb</code>名字包含index的目的就是在表明，这个db是通过index来控制的，你可以通过不同的index打开不同的数据库</p>
                    <p class=".body-1">3. 首先看看简单的应用示例</p>

