          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="indexdb_operation">indexDB其他操作</h3>
                </v-card-title>
                <v-divider></v-divider>
