                    <pre class="code">{{pres[1]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="indexdb_transcation">事务</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[2]}}</pre>
                </v-card-text>
              </v-card>
            </v-hover>
          </v-flex>
          <v-flex sm12>
            <v-hover>
              <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`"
                min-height="300"
              >
                <v-card-title>
                  <h3 id="indexdb_cursor">游标</h3>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <pre class="code">{{pres[3]}}</pre>
                </v-card-text>
              </v-card>
