<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      pres: 
[
    `
    const students = [ // 要存储的数据
        { id: 1, name: '小红', age: 10, sex: '女' },
        { id: 2, name: '小李', age: 17, sex: '男' },
    ];
    
    let db;
    const req = window.indexedDB.open('mydb', 2); // 根据传入的索引打开一个数据库

    const handleInitDb = () => {
        // db的一些监听事件
        db.onerror = function(e) { console.log('数据库出错', e.target.errorCode); }

        db.onabort = function() { console.log('数据库终止', e); };

        db.onclose = function(e) { console.log('数据库意外关闭', e); };

        db.onversionchange = function(e) { console.log('数据库改变', e); };
        
        // indexdb中的表示objectStore对象, createObjectStore可以创建一个表
        const objectStore = db.createObjectStore(
            "students", // 表名
            { 
                keyPath: "id" // 这里定义的keyPath类似主键的作用
            }
        );
        objectStore.createIndex( // 创建索引
            "name", // 索引名
            "name", // 索引的keyPath
            { 
                unique: false // 是否唯一，如果是唯一的重复的数据无法添加
            }
        );

        // 将students数组中的数据添加到db中
        students.forEach(item => {
            objectStore.add(item);
        })
    };

    const handleDb = function() {
        // 创建事务，操作表都需要通过事务实现
        const transaction = db.transaction(['students'], "readwrite");
        // 告诉事务我需要操作哪一个表
        const objectStore = transaction.objectStore('students');

        // 使用游标遍历数据
        objectStore.openCursor(
            null,   // 这里传入的是keyRange对象，keyRange在下一节索引中有讲到
            "next"  // 遍历的方向，表示正序遍历
        ).onsuccess = function(e) {
            const cursor = e.target.result;

            if(cursor) {
                console.log(cursor.value);
                cursor.continue();
            }
        }
    };
    
    req.onsuccess = function(e) {
        db = e.target.result;
        
        // 数据库打开成功，对数据库中的表做相关操作
        handleDb();
    };

    req.onupgradeneeded = function(e) {
        // 新创建数据库，或者更新数据库版本触发此函数
        db = e.target.result;
        
        // 将数据添加到新数据库中
        handleInitDb();
    };

    req.onblocked = function() {
        console.log("由于数据库正在使用，数据库版本暂时不能修改");
    };

    req.onerror = function(e) {
        console.log('数据库打开错误', e);
    };
    `,
    `
    // 获取所有可用数据库的名称和版本号
    indexedDB.databases().then(databases => {
        console.log(databases)
    })

    // 删除数据库
    const dbDelete = window.indexedDB.deleteDatabase("mydb");
    dbDelete.onerror = function(e) {
        console.log("删除数据库失败");
    };
    dbDelete.onsuccess = function(e) {
        console.log("数据库删除成功");
    };

    db.name     // 获取数据库名称
    db.version  // 返回数据库版本
    db.close(); // 关闭数据库连接
    `,
    `
    // 创建事务的几种方式
    // 传入多个表名
    const tc = db.transaction(['mytableName']); 

    // 传入一个表
    const tc = db.transaction('mytableName');   

    // 访问所有表
    const tc = db.transaction(db.objectStoreNames); 

    // 指定权限 
    // readonly只读 
    // readwrite读写
    const tc = db.transaction( 
        'mytableName',
        'readonly' 
    );
    `,
    `
    // 使用游标遍历数据
    const transaction = db.transaction(['students'], "readwrite");
    const objectStore = transaction.objectStore('students');

    // 指定遍历的方向
    1. next：正序
    2. nextunique：正序剔除重复值
    3. prev：逆序
    4. prevunique：逆序剔除重复值

    objectStore.openCursor(
        null,
        "next" 
    ).onsuccess = function(e) {
        const cursor = e.target.result;

        if(cursor) {
            // cursor.direction 获取游标遍历方式
            // cursor.key 获取数据的键
            // cursor.primaryKey 获取数据的主键
            // cursor.source 获取objectStore
            // cursor.value 获取存储的数据

            // 删除数据
            // if(cursor.value.name === '小李'){
            //     cursor.delete().onsuccess = function() { 
            //         console.log('小李删除了');
            //     }
            // }

            // 更新数据
            // if(cursor.value.name === '小李'){
            //     const upDateData = cursor.value;
            //     upDateData.name = "小张";
            //     cursor.update(upDateData).onsuccess = function() { 
            //         console.log('小李更新成小张了');
            //     }
            // }


            console.log(cursor.value);
            cursor.continue(); // 等效 cursor.advance(1); advance可以指定大于1的数表示遍历的时候间隔多列
        }
    }
    `,
    `
    const transaction = db.transaction(['students'], "readonly");
    const objectStore = transaction.objectStore('students');

    // 获取索引
    const nameIndex = objectStore.index('name'); 

    // 索引的一些属性
    /*
        1. nameIndex.keyPath 返回索引名
        2. nameIndex.multiEntry 如果存储的键值是数组类型的数据，multiEntry设置为true就会为数组中的每一项添加一个entry，否则整个数组就是一个entry
        3. nameIndex.name 返回索引名
        4. nameIndex.objectStore 返回原来的objectStore
        5. nameIndex.unique 是否唯一
    */

    // 索引的一些方法
    /*
        1. 获取数据的个数
            // count方法传递参数表示数据的键名，不传参数表示统计所有
            nameIndex.count(
                '小李' 
            ).onsuccess = function(e) { 
                console.log(e.target.result);
            }

        2. 根据键名获取数据
            nameIndex.get('小李').onsuccess = function(e) {
                console.log(e.target.result);
            }

        3. 当索引不是唯一索引，相同的索引可以映射多个值，getAll表示获取所有值
            nameIndex.getAll(
                '小红', // 可选，键名
                1      // 可选，返回数据条数
            ).onsuccess = function(e) { 
                console.log(e.target.result);
            }

        4. 获取索引的所有索引值
            nameIndex.getAllKeys( 
                '小红',  // 可选，键名
                1       // 可选，返回数据条数
            ).onsuccess = function(e) {
                console.log(e.target.result);
            }

        5. 获取指定索引值
            nameIndex.getKey('小红').onsuccess = function(e) {
                console.log(e.target.result);
            }
    */

    // 索引可以直接使用游标来遍历
    const keyRange = IDBKeyRange.only('小红');
    // keyRange.includes('小红'); 判断是否包含传入的键

    nameIndex.openCursor(
        keyRange, 
        "next"
    ).onsuccess = function(e) {
        const cursor = e.target.result;
        if(cursor) {
            console.log(cursor.value);
            cursor.continue();
        }
    }
    
    // IDBKeyRange是用来指定游标的遍历范围，当值是数字的时候效果更明显
    /*
        1. IDBKeyRange.upperBound(x)            <=x
        2. IDBKeyRange.upperBound(x, true)      <x
        3. IDBKeyRange.lowerBound(x)            >=x
        4. IDBKeyRange.lowerBound(x, true)      >x
        5. IDBKeyRange.bound(x, y)              x<= <=y
        6. IDBKeyRange.bound(x, y, true, true)  x< <y
        7. IDBKeyRange.bound(x, y, true, false) x< <=y
        8. IDBKeyRange.bound(x, y, false, true) x<= <y
        9. IDBKeyRange.only(x)                  =x
    */
    `,
    `
    indexdb中表就是objectStore对象

    获取objectStore名称字符串数组

    1. 创建表
        只能在onupgradeneeded监听函数中使用createObjectStore
        db.createObjectStore(
            "mytableName",      // 表名
            { 
                // keypath和audoIncrement必须指定一个
                keyPath: "id", 
                // autoIncrement: true 自增
            }
        );

    2. 删除表
        db.deleteObjectStore("mytableName");

    3. 获取所有表名
        db.objectStoreNames
    
    4. 获取元素
        get中传入的参数是createObjectStore指定的keyPath的值
        objectStore.get(1).onsuccess = function(e) {
            console.log(e.target.result);
        };

    5. 获取所有
        objectStore.getAll(
            1, // 可选，keyPath值
            1  // 可选，获取数据的个数
        ).onsuccess = function(e) {
            console.log(e.target.result);
        }
    
    6. 添加元素
        objectStore.add({
            id: 3,
            name: '小张',
            age: 23,
            sex: '女'
        }).onsuccess = () => {
            console.log('添加成功');
        };

    7. 删除一个元素
        objectStore.delete(1); // 删除指定的keyPath
    
    8. 清空表
        objectStore.clear().onsuccess = function(e) {
            console.log('表已经清空');
        };

    9. 更新数据
        objectStore.put(
            {id: 1, name: "小张", age: 10, sex: "女"}, // 要更新的数据
            '...' // 当主键是autoIncrement需要指定主键，否则不要
        ).onsuccess = function() {
            console.log('更新好了');
        };

    10. 获取数据个数
        objectStore.count(
            1 // 可选，传的是keyPath指定键的键值
        ).onsuccess = function(e) {
            console.log(e.target.result);
        }

    11. 创建索引
        只能在onupgradeneeded监听函数中使用createIndex
        objectStore.createIndex(
            "name", // 索引名
            "name", // 索引的keyPath
            { 
                unique: false, // 是否唯一
                multiEntry: true, // 键值是数组的话，如果设置为true就会为数组中的每一项添加一个entry，否则整个数组就是一个entry
            }
        );

    12. 删除索引
        objectStore.deleteIndex("name");

    13. 获取所有keyPath值
        objectStore.getAllKeys(
            null, // 可选，keyPath值
            1     // 可选，返回数据条数
        ).onsuccess = function(e) {
            console.log(e.target.result);
        }
    
    14. 获取keyPath值
        objectStore.getKey(1).onsuccess = function(e) {
            console.log(e.target.result);
        };
    
    15. 获取指定索引，方便用游标遍历
        const nameIndex = objectStore.index('name'); 

    16. 可以对表直接使用游标遍历
        objectStore.openCursor(
            null,
            "next"
        ).onsuccess = function(e) {
            const cursor = e.target.result;

            if(cursor) {
                console.log(cursor.value);
                cursor.continue();
            }
        }
    `
],
      time: "2019-07-05",
      title: "indexdb浏览器端的简易数据库",
      items: [
        {
          title: "indexDB应用示例",
          target: "#indexdb_demo"
        },
        { divider: true },
        {
          title: "indexDB其他操作",
          target: "#indexdb_operation"
        },
        { divider: true },
        {
          title: "事务",
          target: "#indexdb_transcation"
        },
        { divider: true },
        {
          title: "游标",
          target: "#indexdb_cursor"
        },
        { divider: true },
        {
          title: "索引",
          target: "#indexdb_index"
        },
        { divider: true },
        {
          title: "表",
          target: "#indexdb_table"
        }
      ],
    };
  },
  mounted() {
  },
  methods: {}
};
</script>

